	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Quiet Day","date":"01 Jan 2003","slug":"/2003-01-01-quiet-day/","tags":"Y2003"},"html":"<p>We spent New Year's at home with Christopher. We prepared a simple raclett (gouda cheese, not the real stuff) and fascinated Christopher with a <a href=\"http://www.britannia.org/recipes/showrecipe.php?RecipeID=00000104\">Feuerzangenbowle</a> (punch laced with flaming rum-soaked sugar). For the first time ever we bought fireworks that were going to show him, but he pooped out about 10 pm, so we had to wait impatiently until midnight to shoot them off without him. Today we moped around the house, reading (Mama a mystery, Christopher his new airplane book, me a tech book on <a href=\"http://xml.apache.org/cocoon/\">Cocoon</a>).</p>\n<p>Here's wishing everyone a happy and healthy 2003!</p>"},"next":{"fields":{"title":"Million Dollar Authoring App","date":"31 Dec 2002","slug":"/2002-12-31-million-dollar-authoring-app/"}},"previous":{"fields":{"title":"Five Dollar Bookmarklet","date":"02 Jan 2003","slug":"/2003-01-02-five-dollar-bookmarklet/"}}},{"node":{"fields":{"title":"Million Dollar Authoring App","date":"31 Dec 2002","slug":"/2002-12-31-million-dollar-authoring-app/","tags":"Y2002"},"html":"<p>I was at first confused by Mark Pilgrim's <a title=\"Pushing the envelope [dive into mark]\" href=\"http://diveintomark.org/archives/2002/12/27.html#pushing_the_envelope\">use of the cite tag</a>, but now that he has <a title=\"Million dollar markup [dive into mark]\" href=\"http://diveintomark.org/archives/2002/12/29.html#million_dollar_markup\">clarified</a> that he's not doing it \"for the children\", but rather <a title=\"Tag soup of a new generation [dive into mark]\" href=\"http://diveintomark.org/archives/2002/12/30.html#the_tag_soup_of_a_new_generation\">just for himself</a>, I can see the point. What I need is a million dollar authoring app so I can keep track of all those <a title=\"Couchblog: (X)HTML Attributes used!\" href=\"http://www.couchblog.de/couchblog/archives/2002/12/xhtml_attributes_used.php\">million dollar tags</a> I can use. I don't think a two bit bookmarklet is up to the job.</p>\n<p>(And is it just me, or does Mark's million dollar markup prevent the Movable Type bookmarklet from working in my <a title=\"Mozilla\" href=\"http://www.mozilla.org/\">two-bit browser</a>? Why, I'm surprised that anyone links to him at all. :-) <b>Update</b> Mark points out that it <em>is</em> <a href=\"/2002/12/31/2056.php#comments\">just me</a>.</p>"},"next":{"fields":{"title":"I'll kill that cat!","date":"31 Dec 2002","slug":"/2002-12-31-ill-kill-that-cat/"}},"previous":{"fields":{"title":"Quiet Day","date":"01 Jan 2003","slug":"/2003-01-01-quiet-day/"}}},{"node":{"fields":{"title":"I'll kill that cat!","date":"31 Dec 2002","slug":"/2002-12-31-ill-kill-that-cat/","tags":"Y2002"},"html":"<p>Via <a title=\"WorldWideKlein Weblog\" href=\"http://www.worldwideklein.tv/nothing_more.php?id=2677_0_2_0_M\">WorldWideKlein</a>, the Washington Post has an article on the German New Year's Eve <a href=\"/2001/12/31/1607.php\">tradiition</a>: watching <a title=\"Auld Lang Syne's English Accent (washingtonpost.com)\" href=\"http://www.washingtonpost.com/wp-dyn/articles/A56521-2002Dec30.html\">Dinner For One</a>. The Abendblatt has an <a title=\"Hamburger Abendblatt: Stilvoll stolpern\" hreflang=\"de\" href=\"http://www.abendblatt.de/daten/2002/12/31/109060.html\">interview</a> with the original cameraman. It was originally a throwaway scene for a variety show, NDR first showed it for New Year's Eve 10 years later. Today they are running it <a title=\"Hamburger Abendblatt: Dinner for One\" href=\"http://www.abendblatt.de/daten/2002/12/31/109069.html\">5 times</a>: twice in original black and white, twice colorized, and once <em>up platt</em> (in north German dialect). </p>\n<p>What do Germans see in a English-language theatre sketch from the 1920s? No one knows. <em>\"Maybe it's that our Prussian humor is similar to British humor. But then again, the British don't think it's funny, so maybe it's not British humor after all.\"</em></p>"},"next":{"fields":{"title":"All Wound Up","date":"29 Dec 2002","slug":"/2002-12-29-all-wound-up/"}},"previous":{"fields":{"title":"Million Dollar Authoring App","date":"31 Dec 2002","slug":"/2002-12-31-million-dollar-authoring-app/"}}}],"pathPrefix":"","first":false,"last":false,"index":800,"pageCount":1101}}

